/** Form Elements */
form {
    margin: 0;
}

button {
	border-radius: 0 !important;
	background-image: none;
	-webkit-text-shadow: none;
	text-shadow: none;
	box-shadow: none;
	min-width: 36px !important;
}

input:focus, textarea:focus {
	border-color: #757575;
	color: #212121;
	background-color: #FAFAFA;
	outline: 0;
	outline: thin solid \9;
	box-shadow: none;
}

textarea:focus,
textarea:focus.form-control,
select:focus.form-control,
input[type="text"]:focus {
	border-color: #757575;
	outline: 0;
	outline: thin dotted \9;
	background-image: none;
	/* IE6-9 */
	box-shadow: none;
}

textarea, input {
	border-radius: 0;
}

.input-append input, .input-append select {
	border-radius: 0;
}

.btn-group { vertical-align: inherit; }

.input-group-addon {
	padding: 2px 8px;
	font-size: 20px;
	font-weight: 400;
	line-height: 1;
	text-align: center;
	color: #E3E3E3;
	background-color: #212121;
	border: 1px solid #212121;
	border-radius: 0;
}

/** Start Material UI over-ride classes */
div[class*="MuiCard-root"] {
	border-radius: 0px !important;
	background-color: #FFFFFF !important;
  color: #212121 !important;
}

button[class*="MuiButton-root-"] {
	min-width: 36px !important;
	border-radius: 0px !important;
}

.active {
	background-color: #757575 !important;
  color: #FAFAFA !important;
}

div[class*="MuiCardActions-root"] {
	margin-top:auto !important;
	align-self:flex-end !important;
	align-items: flex-end; 
}

.MuiInputAdornment-positionEnd {
	margin-left: 0 !important;
}

/** END  Material UI over-ride classes */


/* "Toast" Notifications */
.notification-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: flex-start;
  align-content: flex-start;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 999999;
}

.notification-item {
  width: 250px;
  margin: 10px;
  color: #FFF;
}

.notification-item:hover {
  box-shadow: 0 0 10px 0 #212121;
}

.notification-item > p {
  margin: 10px;
}

.notification-item.success {
  background-color: #619020;
}

.notification-item.error {
  background-color:  #DB3E29;
}

.notification-item.info {
  background-color: #757575;
}

.notification-title {
  font-weight: 700;
}

@media all and (max-width: 240px) {
	.notification-container > div {
		padding: 10px;
		width: 11em;
	}

	.notification-container .notification-close-button {
		right: -0.2em;
		top: -0.2em;
	}
}
@media all and (min-width: 241px) and (max-width: 480px) {
	.notification-container > div {
		padding: 10px;
		width: 18em;
	}

	.notification-container .notification-close-button {
		right: -0.2em;
		top: -0.2em;
	}
}

@media all and (min-width: 481px) and (max-width: 768px) {
	.notification-container > div {
		padding: 10px 10px 10px 20px;
		width: 25em;
	}
}

/* Global Resets for tablet and down */

@media all and (max-width: 768px) {
	
	h1 { font-size: 1.6em; }
	h2 { font-size: 1.6em; }
	h3 { font-size: 1.2em;}
	h4 { font-size: 1.2em; }
	h5 { font-size: 1.0em; }

	.MuiButton-textPrimary{
		padding: 0 10px !important;
	}
}


